<template>
	<div class="contata">
		<el-form :model="dynamicValidateForm" ref="dynamicValidateForm" label-width="100px" class="demo-dynamic">
			<el-form-item prop="money" label="总资产">
				<el-input v-model="dynamicValidateForm.money" placeholder="请输入总资产" style="width:200px;"></el-input>
			</el-form-item>
		  <el-form-item
			v-for="(domain, index) in dynamicValidateForm.domains"
			:label="'股票' + (index + 1)"
			:key="domain.key"
		  >
		  <el-form-item :prop="'domains.' + index + '.name'" 
		  :rules="{ required: true, message: '股票名称不能为空', trigger: 'blur'}">
			  <el-input v-model="domain.name" placeholder="请输入股票名称"></el-input>
		  </el-form-item>
		  <el-form-item :prop="'domains.' + index + '.money'"
		  :rules="{ required: true, message: '市值不能为空', trigger: 'blur'}">
		  			  <el-input v-model="domain.money" placeholder="请输入市值"></el-input>
		  </el-form-item>
			<el-form-item :prop="'domains.' + index + '.profit'" :rules="validateTaskName">
				<el-input v-model="domain.profit" placeholder="请输入盈亏"></el-input>
			</el-form-item>
			<el-form-item :prop="'domains.' + index + '.proportion'" :rules="validateproportion">
				<el-input v-model="domain.proportion" placeholder="请输入盈亏比"></el-input> %
			</el-form-item>
			<el-button @click.prevent="removeDomain(domain)" style="margin-left: 10px;" type="danger">删除</el-button>
		  </el-form-item>
		  <el-form-item>
			<el-button type="primary" @click="submitForm('dynamicValidateForm')">提交</el-button>
			<el-button @click="addDomain">新增股票</el-button>
		  </el-form-item>
		</el-form>
		
		<!-- 添加或修改租户管理对话框 -->
		<el-dialog :visible.sync="open" width="400px" append-to-body :close-on-click-modal="false">
			<h4 style="margin-bottom: 20px;display: flex;flex-wrap: wrap;align-items: center;line-height: 23px;">
				<p> <span style="font-weight: normal;color: #606266;">总资产</span> <i class="el-icon-view" @click="ishow=!ishow"></i></p>
				<p style="width: 100%;">
					<span v-if="ishow">{{dynamicValidateForm.money}}</span>
					<span v-if="!ishow" style="padding-left: 7px;">****</span>
				</p>
			</h4>
			<table border="0" cellspacing="0"  id="printrable">
				<th style="text-align: left;font-weight: normal;">市值</th>
				<th style="text-align: right;">盈亏</th>
				<tr v-for="(item,index) in dynamicValidateForm.domains" :key="index">
					<td style="padding-right: 15px;">
						<span :class="item.profit>0? 'red': 'biled'">
							<span>{{item.name}}</span> </br>
							{{item.money}}
						</span>
					</td>
					<td style="text-align: right;">
						<span :class="item.profit>0? 'red': 'biled'">
							{{item.profit}} </br>
							{{item.proportion}}%
						</span>
					</td>
				</tr>
			</table>
		<!-- 	<el-table
			      :data="dynamicValidateForm.domains"
			      style="width: 100%;font-size: 14px;font-weight: normal;">
			      <el-table-column
			        prop="name"
					width="80"
			        label="市值"
					>
					<template slot-scope="scope">
						<span :class="scope.row.profit>0? 'red': 'biled'">
							<span style="font-weight: 800;">{{scope.row.name}}</span> </br>
							{{scope.row.money}}
						</span>
					</template>
			      </el-table-column>
			      <el-table-column
			        prop="profit"
			        label="盈亏">
					<template slot-scope="scope">
						<span :class="scope.row.profit>0? 'red': 'biled'">
							{{scope.row.profit}} </br>
							{{scope.row.proportion}}%
						</span>
					</template>
			      </el-table-column>
				  <el-table-column></el-table-column>
			    </el-table> -->
		  <div slot="footer" class="dialog-footer">
		    <el-button @click="open=false">取 消</el-button>
		  </div>
		</el-dialog>
		
	</div>
	
</template>
<script>
	import '@/assets/fontStyle/index.css'
  export default {
    data() {
      return {
        dynamicValidateForm: {
          domains: [{
            name: '',
			profit:'',
			proportion:''
          }],
        },
		
	 validateTaskName:[
			{required: true, message: '盈亏不能为空', trigger: 'blur'},
			{
			   pattern: /^^(\-|\+)?\d+(\.\d+)?$/,
			   message: "输入正确的盈亏格式",
			   trigger: "blur"
			}
		],
	validateproportion:[
			{required: true, message: '盈亏不能为空', trigger: 'blur'},
			{
			   pattern: /^^(\-|\+)?\d+(\.\d+)?$/,
			   message: "输入正确的盈亏比格式",
			   trigger: "blur"
			}
		],
		open:false,
		ishow:true
      };
    },
    methods: {
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.open=true
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      },
      removeDomain(item) {
        var index = this.dynamicValidateForm.domains.indexOf(item)
        if (index !== -1) {
          this.dynamicValidateForm.domains.splice(index, 1)
        }
      },
      addDomain() {
        this.dynamicValidateForm.domains.push({
          value: '',
          key: Date.now()
        });
      }
    }
  }
</script>
<style scoped>
	.contata{
		font-family: 'AlimamaFangYuanTiVF';
		width: 100%;
		display: flex;
		justify-content: center;
		padding-top: 10%;
		/* background-image: url('@/assets/login-background.jpg'); */
		background-color: #ccc;
		background-size: cover;
		height: 100vh;
		box-sizing: border-box;
	}
	.demo-dynamic{
		width: 75%;
	}
	/deep/.el-form-item__content{
		display: flex;
	}
	.el-input{
		margin-right: 5px;
		margin-left: 10px;
	}
	.red{
		color: #e50000;
	}
	.biled{
		color: #1890ff;
	}
	#printrable{
		font-family: 'AlimamaFangYuanTiVF';
		font-weight: 100;
		line-height: 30px;
	}
	th{
		color: #606266; 
		font-size: 12px;
		font-weight: normal;
	}
	td{
		line-height: 20px;
	}
</style>